<template>
  <div class="wrap">
    <img class="background" src="../assets/bg.png" />
    <b-row class="navbar">
      <b-col class="logo">
        <img class="logo" src="../assets/logo.png" />
      </b-col>
      <b-col class="question">Ešte nemáš účet?</b-col>
      <b-col class="button">
        <b-button class="login" to="/login">Prihlás sa</b-button>
      </b-col>
    </b-row>

    <h1 class="title">Registrácia</h1>
    <ValidationObserver v-slot="{ passes }">
      <form class="form" @submit.prevent="passes(register)">
        <ValidationProvider rules="required|email" v-slot="{ errors }">
          <input class="placeholder" type="email" placeholder="Email" v-model="email" />
          <span class="error">{{ errors[0] }}</span>
        </ValidationProvider>

        <ValidationProvider rules="required|max:16" v-slot="{ errors }">
          <input
            class="placeholder"
            type="text"
            placeholder="Používateľské meno"
            v-model="username"
          />
          <span class="error">{{ errors[0] }}</span>
        </ValidationProvider>

        <ValidationProvider
          vid="password"
          rules="required|password_verification:8,16"
          v-slot="{ errors }"
        >
          <input class="placeholder" type="password" placeholder="Heslo" v-model="password" />
          <span class="error">{{ errors[0] }}</span>
        </ValidationProvider>

        <ValidationProvider
          rules="required|password_verification:8,16|confirmed:password"
          v-slot="{ errors }"
        >
          <input
            class="password"
            type="password"
            placeholder="Potvrdenie hesla"
            v-model="password_confirmation"
          />
          <span class="error">{{ errors[0] }}</span>
        </ValidationProvider>

        <span class="error">{{ error }}</span>
        <b-button class="registration" type="submit">Registrácia</b-button>
      </form>
    </ValidationObserver>
    <b-row class="footer">
      <b-col class="footer-col1">
        &reg; 2020 všetky práva vyhradené
        <span class="footer-logo">CROWD</span>DEMOCRACY
      </b-col>
      <b-col class="footer-col2">Sledujte nás na sociálnych sieťach</b-col>
      <b-col class="footer-col3">social media</b-col>
    </b-row>
  </div>
</template>
<script>
import { ValidationProvider } from "vee-validate";
import { ValidationObserver } from "vee-validate";
import { extend } from "vee-validate";
import { required, email, max, confirmed } from "vee-validate/dist/rules";
import { setInteractionMode } from "vee-validate";
setInteractionMode("eager");

extend("required", {
  ...required,
  message: "Pole nesmie byť prázdne!"
});
extend("email", {
  ...email,
  message: "Nesprávny formát emailu!"
});
extend("max", {
  ...max,
  message: "Užívateľské meno nesmie byť dlhšie ako 16 znakov"
});
extend("confirmed", {
  ...confirmed,
  message: "Heslo sa musí zhodovať"
});

//Custom validation rules

extend("password_verification", {
  validate(value, { min, max }) {
    const passRegex = new RegExp(
      `^(?=.*)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{${min},${max}}`
    );
    return passRegex.test(value);
  },
  params: ["min", "max"],
  message:
    "Heslo musí obsahovať minimálne {min} znakov, najviac {max} znakov, obsahovať aspoň jedno veľké písmeno a jedno číslo."
});

export default {
  components: {
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      email: "",
      username: "",
      password: "",
      password_confirmation: "",
      error: ""
    };
  },
  created() {
    if (this.$store.getters.isLoggedIn) this.$router.push("/home");
  },
  methods: {
    register() {
      let data = {
        email: this.email,
        password: this.password,
        password_confirmation: this.password_confirmation,
        name: this.username
      };
      this.$store
        .dispatch("register", data)
        .then(() => this.$router.push("/home"))
        .catch(() => (this.error = "Invalid credencials"));
    }
  },
  watch: {}
};
</script>

<style lang="css" scoped>
.wrap {
  overflow-x: hidden;
}
.background {
  max-width: 50%;
  height: auto;
  position: absolute;
  align-items: right;
  background-repeat: no-repeat;
  right: 5px;
  margin-top: 90px;
}
.error {
  display: block;
  color: #ff3333;
  font-size: 14px;
  margin-top: 1em;
}

.logo {
  width: 120px;
  margin-left: 2em;
}
.question {
  width: 500px;
  margin-left: 700px;
  padding: 0px;
  font-size: 14px;
  text-align: right;
  margin-top: 4px;
  color: #48486e;
}
.login {
  border-color: #48486e;
  color: #48486e;
  background-color: white;
  width: 140px;
  font-weight: bold;
  font-size: 14px;
  margin-top: 5px;
}
.title {
  margin-top: 150px;
  margin-left: 130px;
  font-size: 55px;
  font-weight: 50;
}
.form {
  margin-left: 130px;
  margin-top: 25px;
  color: black;
}
.form input {
  width: 500px;
  margin-top: 15px;
  height: 50px;
  border: none;
  color: black;
  border-bottom: 1px solid #d7d7c1;
  display: block;
}
.form input:focus {
  outline: none;
}
.form .checkbox {
  width: auto;
  margin: 0;
  height: auto;
}
.registration {
  background-color: #24154b;
  color: white;
  width: 215px;
  height: 60px;
  margin-top: 20px;
  line-height: 43px;
}
.footer {
  margin-top: 440px;
}
.footer-col1 {
  font-size: 14px;
  color: #48486e;
  margin-left: 40px;
  white-space: nowrap;
}
.footer-col2 {
  font-size: 14px;
  color: #48486e;
  margin-left: 115px;
  text-align: right;
}
.footer-col3 {
  color: white;
}
.footer-logo {
  font-weight: 900;
}
</style>
